<template>
  <div id="app">
    <div class="app-container">
      <set-players :savePlayers="savePlayers" v-if="!isGameReady"></set-players>
      <category-random
        :players="players.filter((x) => x.status == 1)"
        :resetPlayers="resetPlayers"
        v-if="isGameReady"
      ></category-random>
    </div>
  </div>
</template>

<script>
import SetPlayers from "./components/setPlayers.vue";
import CategoryRandom from "./components/categoryRandom.vue";

export default {
  name: "App",
  components: {
    SetPlayers,
    CategoryRandom,
  },
  data() {
    return {
      players: [],
      totalPlayers: [],
      isGameReady: false,
    };
  },
  methods: {
    savePlayers: function (players) {
      this.players = players;
      this.isGameReady = true;

      sessionStorage.setItem("players", JSON.stringify(players.map(x => x.name).sort((a, b) => a.localeCompare(b))));
    },
    resetPlayers: function () {
      this.players = this.players.map(function (player) {
        return {
          ...player,
          status: 1
        }
      })
    }
  },
};
</script>

