<template>
  <div class="set-players">
    <h2>Players ({{ players.split("\n").filter((x) => x != "").length }})</h2>
    <textarea
      v-model="players"
      data-gramm_editor="false"
      placeholder="Players name..."
    ></textarea>
    <button
      @click="setPlayers"
      :disabled="players.split('\n').filter((x) => x != '').length <= 1"
    >
      Good
    </button>
  </div>
</template>

<script>
export default {
  name: "setPlayers",
  props: {
    savePlayers: Function,
  },
  data() {
    return {
      players: "",
    };
  },
  mounted() {
    this.players = sessionStorage.getItem("players") ? JSON.parse(sessionStorage.getItem("players")).join("\n") : "";
  },
  methods: {
    setPlayers: function () {
      this.savePlayers(
        this.shufflePlayers(
          this.players.split("\n").filter((x) => x != "").map(function (player) { return { name: player, status: 1, }; })
        )
      );
    },
    shufflePlayers: function (players) {
      for (let i = players.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [players[i], players[j]] = [players[j], players[i]];
      }

      return players;
    },
  },
};
</script>

<style scoped>
.set-players {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 1rem;
}

.set-players > textarea {
  height: 300px;
  padding: 0.5rem;
  resize: none;
  background-color: rgba(255, 255, 255, 0.75);
  border: 0;
  font-family: inherit;
  font-size: 1rem;
}

.set-players > button {
  background-color: #ffffff;
  padding: 0.5rem 1.25rem;
  border: 0;
  justify-self: right;
  text-transform: uppercase;
  font-family: inherit;
  border-radius: 50px;
  color: #eb4d4b;
  cursor: pointer;
}

.set-players > button:disabled {
  opacity: 0.5;
}
</style>